import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import MainIcon from '../images/main.jpg'
import 'font-awesome/css/font-awesome.min.css';
import { useRef } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, Navigation } from "swiper/modules";

import Img1 from '../images/projects/1.jpg'
import Img2 from '../images/projects/2.jpg'
import Img3 from '../images/projects/3.jpg'
import Img4 from '../images/projects/4.jpg'


import icon from '../images/icons/main-page-icons/advantages-1.svg'
import icon2 from '../images/icons/main-page-icons/advantages-2.svg'
import icon3 from '../images/icons/main-page-icons/advantages-3.svg'
import icon4 from '../images/icons/main-page-icons/advantages-4.svg'
import manufacture from '../images/icons/main-page-icons/manufacture-feature-1.svg'
import manufacture2 from '../images/icons/main-page-icons/manufacture-feature-2.svg'

// Стили слайдера 
import "./swiper.css";
import "./pagination.css";
import "./navigation.css";

import Header from "./header"
import "./layout.css"
import './bootstrap.css'
import './Responsive.css'
import "./litle-style.css"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  const progressCircle = useRef(null);
  const progressContent = useRef(null);
  const onAutoplayTimeLeft = (s, time, progress) => {
    progressCircle.current.style.setProperty("--progress", 1 - progress);
    progressContent.current.textContent = `${Math.ceil(time / 1000)}s`;
  };

  return (
    <>
      <div className="hero_area">
        <div className="hero_bg_box">
          <img src={MainIcon} alt=""></img>
        </div>
        <Header siteTitle={data.site.siteMetadata?.title || `Title`} />

        <section className="slider_section ">
          <div id="customCarousel1" className="carousel slide" data-ride="carousel">
            <div className="carousel-inner">
              <div className="carousel-item active">
                <div className="container ">
                  <div className="row">
                    <div className="col-lg-8 col-xl-6 mx-auto ">
                      <div className="iMac:mb-8 iMac:text-9xl font-serif font-bold text-white lg:text-7xl">
                        <h1 className="text">
                          ALTYN JULDYZ
                        </h1>
                        <p>
                          Группа компаний в Казахстане, вносящая огромный вклад в развитие разных отраслей страны
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      {/* COMFORT JAGALAU */}
      <section className="about_section layout_padding background-div">
        <div id="customCarousel1" className="carousel slide" data-ride="carousel">
          <div className="carousel-inner">
            <div className="carousel-item active">
              <div className="container ">
                <div className="row">
                  <div className="col-lg-8 col-xl-6 mx-auto ">
                    <div className="iMac:mb-8 iMac:text-9xl font-serif font-bold text-white lg:text-7xl">
                      <h3 className="2">
                        COMFORT JAGALAU
                      </h3>
                      <p>
                        Мы строим многофункциональный жилой комплекс в новом центре Алматинской области в городе Конаева. Для реализации этой цели мы собрали профессиональную команду в области дизайна, проектирования и строительства
                      </p>
                      <div className="custom-btn btn-14"> Подробнее
                      </div>
                      <div>
                        <h2 className="top ">Наши преимущества</h2>
                      </div>
                    </div>



                    <div className="center-container">
                      <div className="icon-container">
                        <div className="icon">
                          <img src={icon} alt="Icon 3" />
                          <div className="icon-label">Камеры видеонаблюдения</div>
                        </div>
                        <div className="icon">
                          <img src={icon2} alt="Icon 3" />
                          <div className="icon-label">Подземный паркинг</div>
                        </div>
                      </div>

                      <div className="icon-container">
                        <div className="icon">
                          <img src={icon3} alt="Icon 3" />
                          <div className="icon-label">Детские и спортивные площадки</div>
                        </div>
                        <div className="icon">
                          <img src={icon4} alt="Icon 3" />
                          <div className="icon-label">Круглосуточная охрана и пропускная система</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Блок Швейно-обувная фабрика */}
      <section className="about_section layout_padding background-div2 text-white" >
        <div className="Fabrica">
          <h3>Швейно-обувная фабрика</h3>
          <h4>Крупнейший производитель спецодежды и спецобуви</h4>
          <div>
            <div className="flex">
              <div className="flex1">
                <img className="icon-fabrica" src={manufacture}></img>
                <div className="text-fabrica">
                  <h4>Индивидуальная разработка <br />дизайна спецодежды</h4>
                  <p>Как производитель мы с удовольствием разработаем индивидуальный
                    дизайн вашей спецодежды с учетом всех ваших пожеланий и
                    с полным соблюдением фирменного стиля и цветовой гаммы вашей компании.</p>
                </div>

              </div>
              <div className="flex1">
                <img className="icon-fabrica" src={manufacture}></img>
                <div className="text-fabrica">
                  <h4>Индивидуальная разработка <br />дизайна спецодежды</h4>
                  <p>Как производитель мы с удовольствием разработаем индивидуальный
                    дизайн вашей спецодежды с учетом всех ваших пожеланий и
                    с полным соблюдением фирменного стиля и цветовой гаммы вашей компании.</p>
                </div>
              </div>
            </div>
          </div>
        </div>

      </section>

      {/* Блок ИНТЕРНЕТ МАГАЗИН */}
      <section className="about_section layout_padding background-div3">
        <h1>ИНТЕРНЕТ МАГАЗИН</h1>
      </section>

      {/* Слайдер должен быть  */}
      <section className="about_section layout_padding background-div6">
        <Swiper
          spaceBetween={30}
          centeredSlides={true}
          autoplay={{
            delay: 1500,
            disableOnInteraction: false
          }}
          pagination={{
            clickable: true
          }}
          navigation={true}
          modules={[Autoplay, Pagination, Navigation]}
          onAutoplayTimeLeft={onAutoplayTimeLeft}
          className="mySwiper"
        >
          <SwiperSlide><img src={icon}></img></SwiperSlide>
          <SwiperSlide><img src={icon}></img></SwiperSlide>
          <SwiperSlide><img src={icon}></img></SwiperSlide>
          <SwiperSlide><img src={icon}></img></SwiperSlide>
          <SwiperSlide><img src={icon}></img></SwiperSlide>
          <SwiperSlide><img src={icon}></img></SwiperSlide>

          <div className="autoplay-progress" slot="container-end">
            <svg viewBox="0 0 48 48" ref={progressCircle}>
              <circle cx="24" cy="24" r="20"></circle>
            </svg>
            <span ref={progressContent}></span>
          </div>
        </Swiper>
      </section>
      {/* О компании */}
      {/* <section className="about_section layout_padding background-div5">
        <h1>О компании</h1>
      </section> */}

      {/* Наши ценности */}
      <section className="about_section layout_padding background-div6">
        <div>
          <div className="About-style">
            <h1>Наши ценности</h1>
            <h4 className="text-adaptive">Предлагать тот продукт, в котором нуждаются люди, любить свое дело, выполнять работу качественно, постоянно совершенствоваться, используя современные технологии, и конечно, соблюдать срокии договоренности - это основа, на которой компания строит свою деятельность. При этом мы неукоснительно соблюдаем законодательные нормы и требования, требования охраны труда и окружающей среды.</h4>
          </div>
          <div className="flex2">
            <div className="blok-smool">
              <h4 className="text-adaptive2">Ответственность</h4>
              <p>Мы берёмся только за те обязательства, которые можем выполнить с должным качеством</p>
            </div>
            <div className="blok-smool">
              <h4 className="text-adaptive2">Ответственность</h4>
              <p>Мы берёмся только за те обязательства, которые можем выполнить с должным качеством</p>
            </div>
            <div className="blok-smool">
              <h4 className="text-adaptive2">Ответственность</h4>
              <p>Мы берёмся только за те обязательства, которые можем выполнить с должным качеством</p>
            </div>
            <div className="blok-smool">
              <h4 className="text-adaptive2">Ответственность</h4>
              <p>Мы берёмся только за те обязательства, которые можем выполнить с должным качеством</p>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default Layout
